import api from '~api'
import config from '~api-config'

let ckTable = (params) => api.post('/base/depot/list.action',config.api,params);//
let addCk= (params) => api.post('/base/depot/create.action',config.api,params);//
let editCk= (params) => api.post('/base/depot/edit.action',config.api,params);//
let updateCk= (params) => api.post('/base/depot/update.action',config.api,params);//
let deleteCk= (params) => api.post('/base/depot/delete.action',config.api,params);//

export default {
    ckTable,
    addCk,
    editCk,
    updateCk,
    deleteCk
}



